.userinfo{
  width: 62%;
  margin: 0 auto;
  border-radius: 5px;
  background-color: #fff;
  padding: 10px;
}
.userinfo img{
  width: 30px;
  height: 30px;
  border-radius: 5px;
  margin-right: 10px;
}
.userinfo  p{
  margin: 0;
}
.userinfo a{
  text-decoration: none;
  color: #333;
}
.userinfo .recent_topics, .userinfo .recent_replies{
  display: -ms-flexbox;
  display: flex;
  padding: 10px;
  border-top: 1px solid #f0f0f0;
  -ms-flex-align: center;
      align-items: center;
}



