.article{
  width: 62%;
  margin: 0 auto;
  border-radius: 5px;
  background-color: #fff;
  padding: 10px;
}
.article .article-title {
  font-size: 22px;
}
.article .article-info {
  font-size: 12px;
  color: #838383;
  border-bottom: 1px solid #ccc;
  padding-bottom: 20px;
}

.article .article-info>span{
  margin:0 5px;
}
.markdown-text>:first-child{
  margin-top: 0;
}
.topic-content .markdown-text h2,.topic-content .markdown-text h3,.topic-content .markdown-text h4,
.topic-content .markdown-text h5,.topic-content .markdown-text h6 {
  border-bottom: 1px solid #ccc;
  margin: 30px 0 15px;
}
.markdown-text p{
  /*word-wrap: break-word;*/
  word-break: break-all;
}
.article img{
  height: auto;
    max-width: 100%;
    vertical-align: middle;
    border: 0;
}
.topic-content ul{
  list-style-position: inside;
  padding-left:0;
  margin-left: 20px;
}
.reply{
  display: -ms-flexbox;
  display: flex;
  padding: 10px;
  border-top: 1px solid #f0f0f0;
}
.reply img{
  width: 30px;
  height: 30px;
  border-radius: 3px;
  margin-right: 10px;
}

.reply>.reply-author{
  font-size: 12px;
  color: #666;
  margin-right: 10px;
}

.reply11{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  width: 62%;
  margin: 0 auto;
  margin-top: 15px;
}
.reply11 span{
  padding: 10px;
  background-color: #f6f6f6;
  border-radius: 3px 3px 0 0;
}
.reply11 button{
  padding: 3px 10px;
  width: 59px;
  line-height: 27px;
  font-size: 14px;
  margin-top: -39px;
  margin-left: 11px;
  color: #fff;
  border-radius: 3px;
  background-color: #08c;
  border: none;
  cursor: pointer;
  -webkit-transition: all .2s ease-in-out;
  -o-transition: all .2s ease-in-out;
  transition: all .2s ease-in-out;
}
.reply11 button:hover{
  background-color: #05c;
}



