.content{
  width: 62%;
  margin: 0 auto;
  background-color: #fff;
  border-radius: 5px;
  overflow: hidden;
}
.tabs{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: start;
      justify-content: flex-start;
  background-color: #f6f6f6;
  padding: 10px;
}
.tabs>span{
  margin-right: 10px;
  padding: 5px;
  color: #80bd01;
  cursor: pointer;
  border-radius: 4px;
}
.tabs>span:hover{
  color: #08c;
}
.tabs span.active{
  background-color: #80bd01;
  color: white;
}



